  box-sizing: border-box;
  margin: 0px;
  /* font-family: 'Inter', sans-serif; */
  font-family: 'Inter Tight', sans-serif;
  padding: 0px;
}

header {
  width: 100vw;
  height: 66px;
  background-color: #CC5546;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 50px;
}

header button.about {
  background-color: #CC5546;
  color: #FFF;
  padding: 12px 14px;
  display: flex;
  align-items: center;
  gap: 10px;
  border-style: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
}

header button.about:hover {
  background-color: #cc6d61;
}

.content {
  min-height: calc(100vh - 66px);
  display: grid;
  grid-template-columns: 465px 1fr;
  grid-template-rows: auto;
}

.side-bar {
  height: 100%;
  background-color: #F5F5F5;
  padding: 50px 23px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.side-bar button {
  height: 58px;
  display: grid;
  grid-template-columns: 78px 1fr;
  align-items: center;
  border-radius: 10px;
  background-color: #F5F5F5;
  cursor: pointer;
  border-style: none;
}

.side-bar button:hover {
  background-color: #ECECEC;
}

.side-bar button img {
  justify-self: center;
}

.side-bar p {
  justify-self: start;
  font-size: 24px;
  font-weight: 400;
  ;
  color: #2B2B2B;
}

.side-bar h4 {
  margin-top: 50px;
  margin-bottom: 10px;
  color: #2B2B2B;
  font-size: 24px;
  font-weight: 700;
}

.side-bar button.add-project {
  margin-top: auto;
}

.side-bar .add-project p {
  color: #7E7E7E;
}

.projects-container {
  display: flex;
  flex-direction: column;
}

div.project-add-prompt {
  margin-top: auto;
  display: grid;
  grid-template-columns: 1fr 80px;
  grid-template-rows: auto 50px;
  align-items: center;
  row-gap: 5px;
  column-gap: 15px;
  padding: 20px 0px;
}

.project-add-prompt form {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  color: #2B2B2B;
}

.project-add-prompt input {
  border: none;
  border-radius: 15px;
  ;
  height: 45px;
  width: 100%;
  font-size: 28px;
  color: #2B2B2B;
  padding: 0px 15px;
  border: solid 2px #ECECEC;
}

.project-add-prompt input:focus {
  outline: none;
  color: #2B2B2B;
  border: solid 2px #ECECEC;
}

.project-add-prompt button {
  height: 45px;
  margin-top: auto;
  color: #7E7E7E;
  font-size: 35px;
}

.project-add-prompt .icon-container {
  display: flex;
  justify-content: space-between;
  height: 100%;
  grid-column: 1 / -1;
}

.project-add-prompt .icon-container button {
  width: 40px;
  display: flex;
  justify-content: center;
}

.project-add-prompt .icon-container button.active {
  background-color: #c3c3c3;
}


button.active {
  background-color: #ECECEC;
}

/* New task form */

div.dark-screen {
  position: fixed;
  top: 0px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}

form.task-form {
  background-color: #FFF;
  height: 700px;
  width: 600px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 100px;
  padding: 50px;
  display: flex;
  flex-direction: column;
  border-radius: 15px;
}

form.task-form label {
  color: #7E7E7E;
  margin-bottom: 10px;
  font-size: 20px;
}

form.task-form textarea {
  margin-bottom: 35px;
  font-size: 25px;
  padding: 0px 15px;
  border-radius: 5px;
  background-color: #F5F5F5;
  border: none;
  padding: 12px;
  resize: none;
}

form.task-form textarea:focus {
  outline: solid 1px #cdcdcd;
}

form.task-form input {
  height: 40px;
  margin-bottom: 15px;
  font-size: 25px;
  padding: 0px 15px;
  border-radius: 5px;
  background-color: #F5F5F5;
  border: none;
}

form.task-form input[type="date"] {
  color: #2B2B2B;
  font-size: 15px;
}

form.task-form input:focus {
  outline: solid 1px #cdcdcd;
}

form.task-form input.invalid {
  outline: solid 2px #cc6d61;
}

form.task-form .date-created {
  color: #7E7E7E;
  font-size: 14px;
  text-align: end;
}

div.priority-button-container {
  margin-bottom: 15px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 25px;
}

div.priority-button-container button {
  border-radius: 3px;
  height: 40px !important;
  font-size: 14px !important;
  background-color: #F5F5F5;
  cursor: pointer;
  border: none;
  padding: 5px;
  width: 100px;
}

div.priority-button-container button.low:hover,
div.priority-button-container button.low.selected {
  background-color: #4e915c;
  color: white;
}

div.priority-button-container button.medium:hover,
div.priority-button-container button.medium.selected {
  background-color: #6477c4;
  color: white;
}

div.priority-button-container button.high:hover,
div.priority-button-container button.high.selected {
  background-color: #CC5546;
  color: white;
}

form.task-form button {
  margin-top: 15px;
  cursor: pointer;
  border: none;
  background-color: #f5f5f5;
  border-radius: 10px;
  height: 80px;
  font-size: 20px;
  color: #2B2B2B;
}

form.task-form button:hover {
  outline: none;
  background-color: #e2e2e2;
}


/* Project tasks view */

.display-project-container {
  max-width: 1100px;
  width: 100%;
  height: 100%;
  padding: 60px 133px;
  color: #2B2B2B;
}

.workspace .project-name {
  margin-bottom: 60px;
}

.workspace .tasks-container {
  display: flex;
  flex-direction: column;
  border-top: solid 2px #e2e2e2;
}

.tasks-container button {
  display: grid;
  grid-template-columns: 30px 1fr 60px 40px;
  grid-template-rows: 1fr 35px;
  column-gap: 18px;
  justify-content: center;
  align-items: center;
  padding: 10px 15px;
  background-color: #FFF;
  border: none;
}

.tasks-container button:hover {
  background-color: #F5F5F5;
}

.tasks-container button.task {
  min-height: 80px;
  border-bottom: solid 2px #e2e2e2;
}

.tasks-container button.view-button {
  background-color: inherit;
  align-self: center;
  justify-self: center;
  height: 35px;
  width: 100%;
  border-radius: 19px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px;
  cursor: pointer;
  border: none;
}

.tasks-container button.view-button:hover {
  background-color: #eaeaea;
}

.tasks-container button.view-button img {
  width: 20px;
}

.tasks-container button.delete-task {
  display: block;
  grid-column: 4 / 5;
  grid-row: 1 / 3;
  height: 40px;
  width: auto;
  background: transparent;
  cursor: pointer;
  color: #c3c3c3;
  font-size: 30px;
  display: flex;
  justify-content: center;
  border-radius: 8px;
}

.tasks-container button.delete-task:hover {
  background-color: #ECECEC;
}

.tasks-container .task-date {
  grid-column: 2 / 3;
  font-size: 16px;
}

.tasks-container .task-title {
  font-size: 24px;
  min-width: 200px;
  text-align: start;
}

/* .tasks-container .divider {
  height: 1px;
  width: 100%;
  grid-column: 1/5;
  margin-top: 5px
} */


.tasks-container button .task-title,
.tasks-container button .task-date {
  justify-self: start;
}

.tasks-container .task-priority {
  padding: 3px;
  color: white;
  border-radius: 3px;
}

.tasks-container .task-priority.high {
  background-color: #CC5546;
}

.tasks-container .task-priority.medium {
  background-color: #6477c4;
}

.tasks-container .task-priority.low {
  background-color: #4e915c;
}

.tasks-container button.add-task-button {
  height: 60px;
  cursor: pointer;
  display: flex;
  align-content: center;
  justify-content: start;
}

.tasks-container button.add-task-button p {
  font-size: 24px;
  text-align: start;
  color: #7E7E7E;
}

.tasks-container button.done {
  background-color: #ededed;
  position: relative;
}

.tasks-container button.done p.task-title {
  text-decoration: line-through;
  text-decoration-color: #CC5546;
}

.tasks-container button.done::before {
  font-size: 45px;
  content: "âœ”";
  position: absolute;
  top: -6px;
  left: 15px;
  color: #2B2B2B;

}


/* Today tab grid-layout */

.workspace.today,
.workspace.all,
.workspace.important {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-right: 133px;
}

.workspace.today .left-side .display-project-container,
.workspace.today .right-side .display-project-container,
.workspace.all .right-side .display-project-container,
.workspace.all .left-side .display-project-container,
.workspace.important .left-side .display-project-container,
.workspace.important .right-side .display-project-container {
  height: auto;
  padding-bottom: 0px;
  padding-right: 0px;
